export default {
	props: [
		'enableShare'
	],
	data() {
		return {
			shareText: 'uni-app可以同时发布成原生App、小程序、H5，邀请你一起体验！',
			href:"https://uniapp.dcloud.io",
			image: '',
			// 1: 文字, 2: 图片, 0: 图文, 5: 小程序
			shareType:1,
			providerList: [
				{
					id: 'weixin',
					name: '微信好友',
					sIcon: 'icon-weixin',
					sClass: 'weixin',
				},
				{
					id: 'weixin',
					name: '朋友圈',
					sIcon: 'icon-weixin',
					sClass: 'pengyouquan',
				},
				{
					id: 'sinaweibo',
					name: '新浪微博',
					sIcon: 'icon-xinlangweibo',
					sClass: 'weibo',
				},
				{
					id: 'qq',
					name: 'QQ好友',
					sIcon: 'icon-QQ',
					sClass: 'qq',
				}
			]
		}
	},
	methods: {
		toggle(isEnable) {
			this.$emit('toggle', isEnable);
		},
		share(provider, index) {
			if (provider.id === 'weixin' && index === 0) {
				uni.share({
					provider: provider.id,
					title: 'uni-app分享测试(title)',
					scene: 'WXSceneSession',
					type: 0,
					href: 'http://uniapp.dcloud.io/',
					summary: "uni-app分享测试(summary)",
					imageUrl: "http://uc.bisicdn.xyz/uc_server/images/noavatar_middle.gif",
					success: (res => {
						console.log(JSON.stringify(res))
					})
				})
			} else if (provider.id === 'weixin' && index === 1) {
				uni.share({
					provider: provider.id,
					title: 'uni-app分享测试(title)',
					scene: 'WXSenceTimeline',
					type: 0,
					href: 'http://uniapp.dcloud.io/',
					summary: "uni-app分享测试(summary)",
					imageUrl: "http://uc.bisicdn.xyz/uc_server/images/noavatar_middle.gif",
					success: (res => {
						console.log(JSON.stringify(res))
					})
				})
			} else if (provider.id === 'sinaweibo') {
				uni.share({
					provider: provider.id,
					title: 'uni-app分享测试(title)',
					type: 0,
					href: 'http://uniapp.dcloud.io/',
					summary: "uni-app分享测试(summary)",
					imageUrl: "http://uc.bisicdn.xyz/uc_server/images/noavatar_middle.gif",
					success: (res => {
						console.log(JSON.stringify(res))
					})
				})
			} else  if (provider.id === 'qq') {
				uni.share({
					provider: provider.id,
					title: 'uni-app分享测试(title)',
					type: 0,
					href: 'http://uniapp.dcloud.io/',
					summary: "uni-app分享测试(summary)",
					imageUrl: "http://uc.bisicdn.xyz/uc_server/images/noavatar_middle.gif",
					success: (res => {
						console.log(JSON.stringify(res))
					})
				})
			}
		}
	}
}
